import React from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import { NavLink } from 'react-router-dom';

const BarABiere = () => {
    return (
        <>
            <Header />
            <main className='sharedMain beer'>
                <section className="sharedIntro beer_intro">
                    <div className="sharedIntro_container">
                        <h1 className="shared-h1">
                            Des bires issues de brasseries locales
                        </h1>
                    </div>
                </section>

                <section className="sharedSectionA">
                    <div className="sharedSectionA_container beer_pres">
                        <p className='textA'>
                            Dans notre bar  bire, nous accordons une importance particulire  la qualit de nos boissons. C'est pourquoi nous proposons uniquement des bires artisanales soigneusement slectionnes, provenant de brasseries locales renommes.
                        </p>
                        <p className='textA'>Notre personnel qualifi est  votre dispositionafin de vivre une exprience inoubliable au sein de notre bar  bire. N'hsitez pas  solliciter leurs conseils aviss pour vous guider dans votre choix parmi notre large slection de bires. <br />
                            Chez BarbeerShop, nous nous engageons  vous offrir le meilleur de l'univers des bires artisanales locales
                        </p>
                    </div>
                </section>

                <section className='sharedSectionB'>
                    <div className="sharedSectionB_container beer_services">
                        <h2 className="shared-h2">Dcouvrez notre carte</h2>
                        <div className="beer_services_container">
                            <NavLink to="/bar_a_biere/brasserie_montchat">
                                <img
                                    src={`${process.env.PUBLIC_URL}/img/logo_montchat.jpg`}
                                    alt=""
                                />
                            </NavLink>
                            <NavLink to="/bar_a_biere/brasserie_georges">
                                <img
                                    src={`${process.env.PUBLIC_URL}/img/logo_georges.jpg`}
                                    alt=""
                                />
                            </NavLink>
                            <NavLink to="/bar_a_biere/la_canute">
                                <img
                                    src={`${process.env.PUBLIC_URL}/img/logo_canute.jpg`}
                                    alt=""
                                />
                            </NavLink>
                            <NavLink to="/bar_a_biere/ninkasi">
                                <img
                                    src={`${process.env.PUBLIC_URL}/img/logo_ninkasi.jpg`}
                                    alt=""
                                />
                            </NavLink>
                            <NavLink to="/bar_a_biere/softs">
                                <img
                                    src={`${process.env.PUBLIC_URL}/img/logo_softs.jpg`}
                                    alt=""
                                />
                            </NavLink>

                        </div>
                    </div>
                </section>
            </main>
            <Footer />
        </>
    );
};

export default BarABiere;