import React from 'react';
import Header from '../../components/Header';
import Footer from '../../components/Footer';
import productData from '../../data/boutique/products.json';



const Baume = () => {
    const product = productData.products.find(p => p.name === "Baume 2 en 1");

    if (!product) {
        return (
            <>
                <main className="sharedMain">
                    <h1 className='shared-h1'>Produit introuvable !</h1>
                </main>
            </>
        )

    }

    return (
        <>
            <Header />
            <main className='productMain'>
                <section className="productIntro">
                    <img
                        className='productImg'
                        src={`${process.env.PUBLIC_URL}${product.image}`}
                        alt={product.name}
                    />
                    <h1 className='shared-h1'>{product.name}</h1>
                </section>

                <section className="productDescription">
                    <div className='productContainer'>
                        <h2 className='shared-h2'>Description</h2>
                        {product.description.map((data, dataIndex) => (
                            <p
                                className='product-p'
                                key={dataIndex}>
                                {data}
                            </p>
                        ))}
                    </div>

                    <div className='productContainer'>
                        <h2 className="shared-h2">La marque BarbeerShop</h2>
                        <p className="product-p">
                            BarbeerShop : l'essence de l'lgance masculine, pour les hommes modernes qui savent apprcier le luxe et l'authenticit. Dcouvrez une gamme exclusive inspire par l'Ayurveda, alliant tradition millnaire et qualit suprieure. Rvlez votre charisme naturel et votre confiance avec des soins qui refltent votre style de vie.
                        </p>
                    </div>

                    <div className='productContainer'>
                        {product.ingredients && (
                            <>
                                <h2 className="shared-h2">Ingrdients</h2>
                                <p className="product-p">{product.ingredients}</p>
                            </>
                        )}
                    </div>

                    <div className='productPriceContainer'>
                        {product.price.map((price, priceIndex) => (
                            <div className='productPrice'>
                                <div key={priceIndex}>
                                    {price}
                                </div>
                                <img
                                    src={`${process.env.PUBLIC_URL}/img/addBtn.svg`}
                                    alt="addBtn"
                                />
                            </div>
                        ))}

                    </div>
                </section>

            </main>
            <Footer />
        </>
    );
};

export default Baume;